.toasts {
  position: fixed;
  max-width: 400px;
  bottom: 24px;
  right: 24px;
  z-index: 1;
}
@media only screen and (max-width: 500px) {
  .toasts {
    width: 100%;
    max-width: 100%;
    bottom: -5px;
    right: 0;
  }
}

.toast {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: start;
      align-items: flex-start;
  background-color: gray;
  font-weight: 200;
  color: white;
  text-align: left;
  padding: 0px 20px;
  margin: 5px 0;
  height: 48px;
}
.toast__content {
  overflow: hidden;
  white-space: nowrap;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}

.toast-animation-enter {
  opacity: 0.01;
  height: 0;
}
.toast-animation-enter-active {
  opacity: 1;
  height: 48px;
  -webkit-transition: opacity 300ms ease-in, height 300ms ease;
  -o-transition: opacity 300ms ease-in, height 300ms ease;
  transition: opacity 300ms ease-in, height 300ms ease;
}
.toast-animation-exit {
  opacity: 1;
  -webkit-transform: translateX(0);
      -ms-transform: translateX(0);
          transform: translateX(0);
}
.toast-animation-exit-active {
  opacity: 0.01;
  -webkit-transform: translateX(40px);
      -ms-transform: translateX(40px);
          transform: translateX(40px);
  -webkit-transition: opacity 200ms ease-in 100ms, -webkit-transform 300ms ease-in;
  transition: opacity 200ms ease-in 100ms, -webkit-transform 300ms ease-in;
  -o-transition: opacity 200ms ease-in 100ms, transform 300ms ease-in;
  transition: opacity 200ms ease-in 100ms, transform 300ms ease-in;
  transition: opacity 200ms ease-in 100ms, transform 300ms ease-in, -webkit-transform 300ms ease-in;
}



